.App {
  text-align: center;
  font-family: 'Raleway';
  font-size: 0px;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.badge{
  position: fixed;
  top: 20px;
  left: 28px;
  text-decoration: none;
  color: #f42a70;
  font-size: 25px;
  border-style: solid;
  border-width: 1px;
  height: 31px;
  width: 31px;
}
