.home{
	background: #101010;
	color: white;
	position: relative;
	overflow: hidden;

	.bg{
		max-height: 100vh;
		max-width: 100vw;
		position: absolute;
		opacity: 0.7;
	}

	.nav-buttons{
		color: white;
		font-size: 20px;
		font-weight: 100;
		position: absolute;
		top: 50%;
		transform: translate(0%, -50%);

		.nav{
			display: block;
			margin: 30px 20px;
			cursor: pointer;
		}
	}

	.section{
		width: 70%;
		vertical-align: middle;
		display: inline-block;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		padding: 50px;

		h1{
			width: 90%;
			display: inline-block;
			font-size: 100px;
			font-weight: 600;
			margin: 0px;
		}
	
		h2{
			width: 90%;
			display: inline-block;
			font-size: 25px;
			font-weight: 300;
			margin: 0px;
		}

		a, p{
			text-decoration: none;
			color:white;
			width: 40%;
			font-size: 14px;
			display: inline-block;
			vertical-align: middle;
		}
	}

	.profilePic{
		border-style: solid;
		border-color: #444;
		border-radius: 100%;
		width: 300px;
	}
}

.contact{
	color: white;
	font-size: 30px;
	border-style: solid;
	border-width: 1px;
	padding: 5px 10px;
	text-decoration: none;
}